/*  -  */
@media print {
  /*  */
  body * {
    visibility: hidden !important;
  }
  
  /*  */
  .invoice-print-container,
  .invoice-print-container * {
    visibility: visible !important;
  }
  
  /*  */
  .invoice-print-container {
    position: absolute !important;
    left: 0 !important;
    top: 0 !important;
    width: 100% !important;
    margin: 0 !important;
    padding: 20px !important;
    box-shadow: none !important;
    border: none !important;
    background: white !important;
  }
  
  /*  */
  @page {
    margin: 20mm;
    size: A4;
  }
  
  /*  */
  .invoice-print-container table {
    page-break-inside: auto !important;
  }
  
  .invoice-print-container tr {
    page-break-inside: avoid !important;
    page-break-after: auto !important;
  }
  
  /*  */
  .invoice-print-container .invoice-header {
    page-break-after: avoid !important;
  }
  
  .invoice-print-container .invoice-totals {
    page-break-before: avoid !important;
  }
  
  /*  */
  .invoice-print-container * {
    color: black !important;
    background: white !important;
  }
  
  /*  */
  .invoice-print-container a,
  .invoice-print-container button {
    display: none !important;
  }
  
  /*  */
  .invoice-print-container table,
  .invoice-print-container th,
  .invoice-print-container td {
    border: 1px solid #ddd !important;
  }
  
  /*  */
  .invoice-print-container {
    font-size: 12pt !important;
    line-height: 1.4 !important;
  }
  
  .invoice-print-container h1 {
    font-size: 18pt !important;
  }
  
  .invoice-print-container h2 {
    font-size: 16pt !important;
  }
  
  .invoice-print-container h3 {
    font-size: 14pt !important;
  }
}